.sendForm {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 20px 0 20px;


}


.textarea {
    justify-content: space-between;
    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
    width: 90%;
    display: flex;
    min-height: 40px;
    align-items: flex-start;
    padding: 10px 0 0 30px;

}

.button {
    display: flex;
    flex-direction: row;
    background: #0066CC;
    border-radius: 20px;
    width: 7%;
    align-items: center;
    padding-left: 25px;
    color: #FFFFFF;

}

